




























import uniList from '@dcloudio/uni-ui/lib/uni-list/uni-list.vue';
import uniListItem from '@dcloudio/uni-ui/lib/uni-list-item/uni-list-item.vue';
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			followed: false
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '设置'
		});
	},
	methods: {
		logout: function() {
			uni.showLoading({
				title: '正在退出'
			})
			console.log('log out');
			uni.removeStorageSync('login_key');
			uni.hideLoading()
			/* uni.showToast({
				title: '已经退出当前账号',
				duration: 2500
			}); */
				uni.showModal({
					title: '提示',
					content: '确认退出吗？',
					cancelText:'再等等',
					cancelColor:'#EA6F5A',
					success: function(res) {
						if (res.confirm) {
							uni.navigateBack();
						} else if (res.cancel) {
							console.log('用户点击取消')
						};
					}
				});
			
		},
		black: function() {
			this.followed = !this.followed;
		}
	}
};
